// 1. Colors
// ==========================================================================

$primary-color: #2ec4c3 !default;
$primary-color-light: lighten($primary-color, 15%) !default;
$primary-color-dark: darken($primary-color, 15%) !default;

$secondary-color: #a0c647 !default;
$success-color: color("green", "base") !default;
$error-color: #af0000 !default;
$link-color: #2ec4c3 !default;


$c1						: #eaeaea;  // body background
$c2						: #005658;
$c3						: #757d8b;  // body text color
