/**
 * Base SCSS
 *
 * See vendors/components for c0re groups of styles (part of the materialize framework)
 *
 * These control the layout of the basic interface and essential layout elements.
 * The c0re__ namespace is meant to make visually identifying these c0re styles easy
 * and to avoid conflicts with plugins, etc.
 *
 * Styles for individual pages should live in the pages.scss
 *
 */


//Default styles

html {
 box-sizing: border-box;
}
*, *:before, *:after {
 box-sizing: inherit;
}

body {
  // display: flex;
  // min-height: 100vh;
  // flex-direction: column;
  background-color: $c1;
 // background: $c1 url('../img/bg.jpg') no-repeat top center;
  //background-size: 100% auto;
  //@include texture(180deg, $c1, #f1fafa, 24, 1);

}

main {
  // flex: 1 0 auto;
}

ul {
  &:not(.browser-default) {
    padding-left: 0;
    list-style-type: none;

    li {
      list-style-type: none;
    }
  }
}

a {
	color: $link-color;
	text-decoration: none;
	cursor:pointer;

  // Gets rid of tap active state
  -webkit-tap-highlight-color: transparent;
}

h1, h2, h3 {
  font-weight:100;
//  text-shadow: 1px 1px 0 $c9;
  color: $titles;
}
h1, h2 {
  text-transform:uppercase;
}


img,
video {
	height: auto; /* Make sure images are scaled correctly. */
	max-width: 100%; /* Adhere to container width. */
}

/* Make sure embeds and iframes fit their containers. */

embed,
iframe,
object {
	margin-bottom: 1.5em;
	max-width: 100%;
}


/* Text meant only for screen readers. */

.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	height: 1px;
	overflow: hidden;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important; /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	-webkit-border-radius: 3px;
	border-radius: 3px;
	-webkit-box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: 700;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000; /* Above WP toolbar. */
}




/* ----------------------------------------------------------------------------
 * C0re Namespace Styles
 * ------------------------------------------------------------------------- */
#c0re__interface {
	display:table;   /* set for most interoperable solution for dynamic (undefined-height) sticky footer */
	table-layout:fixed;
	width:100%;
	height:100%;
	border:0;
	border-collapse:collapse;
	border-spacing:0px;
	overflow:hidden; /* important to avoid horizontal scroll */
	
	//@include radial-gradient(rgba($c1, 1) 0%, rgba($c8, 0.3));
	//background: url('../img/gradient-top.png') no-repeat top center;
	background-size: contain;
	
	
	
}

#c0re__outer {
	display:table-row;
	height:100%;
	width:100%;
	vertical-align:middle;
}

.c0re__view {
	*zoom: 1;
	width:100%;
	height:auto;
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
	display:block;
	//display:flex;
	overflow:hidden;
	
	&:before {
	  display:table-cell;
	  width:100%;
	  height:100%;
	  content:'';
	}
	
	&:after {
	  clear:both;
	}
}
.c0re__gutter {
  max-width: 125px !important;
  //display:inline-block;
  //margin:0;
  padding:0;
  min-height:100%;
  position:relative;
  vertical-align:top;
  
  .rail {
	border-right:1px solid $c2;
	min-height:100%;
	position:relative;
	font-size:1em;
  }
  .section-heading {
	right:-122px;
	top:125px;
	width:125px;
	height:auto;
	position:absolute;
	@include rotate(-90);
  }
  .section-title {
	color:$titles;
	font-size:1.3em;
	text-transform:uppercase;
	text-align:left;
  }
  .section-subheading {
	color: $c5;
	text-align:left;
	font-size:90%;
  }
  .section-filters {
	position:absolute;
	top:20em;
	text-align:right;
	right:-28px;
  }
  .section-results {
	color: $titles;
	font-size: 11px;
	position:absolute;
	top:28.5em;
	text-align:right;
	right:5em;
	
	span {
	  color: $c6;
	  display:block;
	}
  }
  
  
}
.c0re__content {
	position:relative;
	padding:2em 4em;
	//display:inline-block;
	//@include calc(width, '100% - 9em');
	
	section {
	  //visibility:hidden;
	}
	
	&.layout-standard {
	  padding-top:200px;
	}
}

.c0re__inner {
  //display:table-cell;
  position:relative;
}
  
.c0re__centered {
    
