/* ----------------------------------------------------------------------------
 * Social Links
 * ------------------------------------------------------------------------- */

.social-links {
  padding: 0;
  list-style: none;
  margin: 0;
  float:right;
  width:100%;
  font-family:"FontAwesome";
  text-align:center;
	
}
.social-links li {
  margin: 10px 20px;
  display: inline-block;
  font-size: 40px;
}
.social-links li a {
  color: $primary-color;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  
  &:hover {
	  color:$black;
  }
}
.social-links.smaller li {
	font-size:24px;
}
.social-links.smaller {
	float:left;
}



