/*
 * Work / Credits
 *
 */

.section-overview {
  position:fixed;
  bottom:1.5em;
  right:3em;
  width:100%;
  max-width:450px;
  color: $c3;
  z-index:6;
  font-size:12px;
  text-align:right;
  
  @media #{$medium-and-down} {
	@include center(true, false);
	position:relative;
	right:auto;
	text-align:center;
	padding:2em 1em;
  }
}

.heading {
  text-align:center;
}
#credits {
  width:100%;
  max-width:60em;
  margin:0 auto;
  text-align:center;
  
  &.list {
	
	.subheadings {
	  display:flex;
	  
	  .heading-name {
		text-align:right;
		border-bottom:1px solid rgba($white, .5);
		padding:0;
		margin-right:2.5em;
	  }
	  .heading-role {
		text-align:left;
		border-bottom:1px solid rgba($white, .5);
		padding:0;
		margin-left:2.5em;
	  }
	  
	  span {
		border-bottom:2px solid $white;
	  }
	}
	
	.theTitle {
	  text-align:right;
	  padding:0.25em 2em 0.25em 0.25em;
	  color:#404348;
	  font-weight:bold;
	  font-size:12px;
	  line-height:24px;
	  
	  @include transition(all 0.25s);
	}
	.theThumb {
	  max-width:30px;
	  padding:0;
	  margin:0;
	}
	
	
	.theMetaData {
	  text-align:left;
	  padding:0.25em 0.25em 0.25em 2em;
	  font-size:12px;
	  color:#adadad;
	  
	  @include transition(all 0.25s);
	}
	.theCredit {
	  font-size:12px;
	  color:#adadad;
	  line-height:24px;
	}
	.theAttribution {
	  display:none;
	}
	.more {
	  float:right;
	  display:none;
	}
	
	.thumb-square {
	  width:30px;
	  height:30px;
	  vertical-align:middle;
	  padding:0;
	}
	.thumb-wide {
	  display:none;
	}
	
	
	.work {
	  width:100%;
	  margin:2px;
	  
	  &:hover {
		.theMetaData {
		  @include linear-gradient(to right, #d6d8dd 0%, #afb1b6);
		  color:#eaeaea;
		  
		  @include transition(all 0.25s);
		}
		.theCredit {
		  color: #fff;
		}
		.more {
		  color:#eaeaea;
		  display:block;
		}
		.theTitle {
		  color:#dee8f8;
		  //background:#484c55;
		  @include linear-gradient(to right, #484c55 20%, rgba(#484c55, 0.8));
		  
		  @include transition(all 0.25s);
		}
	  }
	}
	
	
	@media #{$small-and-down} {
	  
	}
	
  }
  
  &.grid {
	
	.subheadings {
	  display:none;
	}
	
	.thumb-square {
	  display:none;
	}
	
	@media #{$small-and-down} {
	  
	}
	
  }
  
}


/*
 * Project / Work Item
 *
 */
.theWork {
  padding:0;
  
  .theContent {
	width:100%;
	max-width:1080px;
	padding:0 2em 2em 2em;
	margin:0 auto;
  }
  .theDescription {
	color: $c3;
	
	p {
	  color:$c3;
	  font-size:90%;
	}
  }
  .theLogo {
	background:#e3e3e3;
	padding:1em;
	position:relative;
	min-height:120px;
	margin-bottom:2em;
	
	img {
	  @include center(true, true);
	  border:none;
	  border-width:0;
	  outline:none;
	  width:60%;
	}
  }
  
  h1 {
	text-align:center;
	margin:0 auto 5px auto;
	color:$titles;
	font-family:"Roboto";
	font-weight:200;
	text-transform:none;
	letter-spacing:0.05em;
	font-size:1.75em;
  }
  .roles {
	margin:0 auto;
	text-align:center;
  }
}


.swiper-container {
	width: 100%;
	height: 80px;
	//height: 40vh;
	margin: 20px auto;
}
.swiper-slide {
	text-align: center;
	font-size: 18px;
	background: none;
	width: auto;
	/* Center slide text vertically */
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	-webkit-justify-content: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;
	
	.gallery-image, a {
	  //height:100% !important;
	  //width:auto !important;
	  background:#84959c;
	  width:80px;
	  height:80px;
	  overflow:hidden;
	  @include opacity(.5);
	  
	  &:hover {
		@include opacity(1);
	  }
	}
}
.swiper-slide:nth-child(2n) {
	
}
.swiper-slide:nth-child(3n) {
	
}
