/*************** F O O T E R ****************/

.c0re__footer {
  background:transparent;
  display:table-row;
  text-align:center;
    
  

  .gradient-footer {
	//@include linear-gradient(to top, $c8 20%, rgba($c8, 0), $fallback:transparent);
	width:100%;
	position: absolute;
	bottom:-40px;
	left:0;
	height:100%;
	height:100vh;
	
	
	/*@media (orientation: portrait) {
	  height: 40%;
	  height: 40vh;
	}*/
  }
}
footer {
  position:relative;
}
footer.page-footer {
  background:transparent;
  
  .footer-copyright {
	  font-size:80%;
	  padding:5px;
	  color: $c2;
	  background:none;
	}
	.legal {
	  display:inline-block;
		
	  .copyright {
		border-right: 1px solid $c5;
		padding-right:1em;
	  }
	  
	  li {
		display:inline-block;
		padding-left:10px;
		color:$c2;
	  }
	  
	  a {
		color:$c2;
	  }
	}
  
  
  .footer-nav {
  
	  li {
		display:inline-block;
		clear:none;
		padding:5px;
		
		@media (max-width: 700px) {
		  display: block;
		  clear: both;
		}
	  }
	  .footer-nav-left {
		padding-right:10px;
		text-align:right;
	  }
	  .footer-nav-right {
		padding-left:10px;
		text-align: left;
	  }
	  
  }
	
	
  #footer-logo {
	  text-align:center;
	  
	  .brand-logo {
		width:100%;
		display:block;
		margin:0 auto;
	  }
  }
}

#footer-wrap {
  //@include texture(125deg, #b4dcea, lighten(#b4dcea, 5%), 24, 0);
  padding:2em;
  
  form {
	margin: 2em 10% 100px 10%;
	
	@media (max-width: 900px) {
	  margin: 2em 2% 100px 2%;  
	}
	
  }
  
  h2 {
	color:$c10;
	text-transform:none;
	text-shadow:none;
	margin-bottom:20px;
  }
  
  
}


#sound-control {
  right:auto;
  left:23px;
  
  .btn-large {
	background: $c4;
  }
  
  & > ul {
	left:48px;
	right: auto;
	text-align:left;
	background: $c4;
	min-width:100%;
	width:100vw;
	padding-left:64px;
  }
}
