/*************** H E A D E R ****************/

.c0re__header {
  position:relative;
  width:100%;
  display:table-row;
  //z-index:10;
  top:0;
  left:0;
  //background: url('../img/gradient-top.png') no-repeat top center;
  background-size: contain;
}

/*#menuIcon {
  display:block;
}
#closeMenu {
  display:none;
}
*/


.gradient-header {
  @include linear-gradient(to top, rgba($c1, 1) 30%, rgba($c1, 0), $fallback:transparent);
  position:fixed;
  bottom:0;
  width:100%;
  left:0;
  height:20%;
  height:20vh;
  max-height:500px;
  min-height:80px;
  z-index:5;
}



#main-nav {
  padding:20px 0;
  position:relative;
  z-index:10;
  
  .nav-link {
	width:100%;
	text-align:right;
	
	.leader {
	  border-bottom:1px solid rgba(#6b8a1d, 0.9);
	  width:100px;
	  vertical-align:bottom;
	  display:inline-block;
	}
	span {
	  border-bottom:1px solid #c4cfa7;
	  display:inline-block;
	}
	
	a {
	  padding: 0 0 0 0;
	  text-transform:uppercase;
	  font-weight:400;
	  letter-spacing:2px;
	  
	  &.active {
		font-weight:600;
		font-style:italic;
	  }
	}
  }
  
  .auxilary {
	clear:none;
	display: inline-block;
	border: none;
	padding:5px;
	
	a {
	  padding:5px 0;
	  color:$secondary-color;
	  
	  &:hover {
		background:none !important;
	  }
	}
	i {
	  height:30px;
	  color:$secondary-color;
	}
  }
  
  li[data-type="chat"] {
	  clear:both;
	  margin-left:30px;
  }
}

nav {
  &.nav-extended {
    height: auto;

    .nav-wrapper {
      min-height: $navbar-height-mobile;
      height: auto;
    }

    .nav-content {
      position: relative;
      line-height: normal;
    }
  }

  color: $navbar-font-color;
  @extend .z-depth-1;
  background-color: transparent;
  width: 100%;
  height: $navbar-height-mobile;
  line-height: $navbar-line-height-mobile;
  box-shadow: none;

  a { color: $navbar-font-color; }

  i,
  [class^="mdi-"], [class*="mdi-"],
  i.material-icons {
    display: block;
    font-size: 32px;
    height: $navbar-height-mobile;
    line-height: $navbar-line-height-mobile;
  }

  .nav-wrapper {
    position: relative;
    height: 100%;
  }

  @media #{$large-and-up} {
    a.button-collapse { display: none; }
  }


  // Collapse button
  .button-collapse {
    float: left;
    position: fixed;
	bottom:1em;
	left:16em;
    z-index: 6;
    height: $navbar-height-mobile;
    margin: 18px;
	

    i {
      height: $navbar-height-mobile;
      line-height: $navbar-line-height-mobile;
    }
  }


  // Logo
  .brand-logo {
    position: fixed;
    color: $navbar-font-color;
    display: inline-block;
    font-size: $navbar-brand-font-size;
    padding: 0;
    white-space: nowrap;
	bottom:1em;
	left:1em;
	width:20%;
	width:20vw;
	max-width:200px;
	z-index:6;

    &.center {
      left: 50%;
      transform: translateX(-50%);
    }
	
	img {
	  width:100%; // fix for using svg
	  vertical-align:bottom;
	}

    @media #{$medium-and-down} {
      left: 1em;
	  transform: none;

      &.left, &.right {
        padding: 0;
        transform: none;
      }

      &.left { left: 0.5rem; }
      &.right {
        right: 0.5rem;
        left: auto;
      }
    }

    &.right {
      right: 0.5rem;
      padding: 0;
    }

    i,
    [class^="mdi-"], [class*="mdi-"],
    i.material-icons {
      float: left;
      margin-right: 15px;
    }
  }


  // Title
  .nav-title {
    display: inline-block;
    font-size: 32px;
    padding: 28px 0;
  }


  // Navbar Links
  ul {
    margin: 0;

    li {
      transition: background-color .3s;
      //float: left;
	  display:block;
	  clear:both;
      padding: 0;
	  

      &.active {
        background-color: rgba(0,0,0,.1);
      }
    }
    a {
      transition: background-color .3s;
      font-size: $navbar-font-size;
      color: $navbar-font-color;
      display: block;
      padding: 0 15px;
      cursor: pointer;

      &.btn, &.btn-large, &.btn-flat, &.btn-floating {
        margin-top: -2px;
        margin-left: 15px;
        margin-right: 15px;

        & > .material-icons {
          height: inherit;
          line-height: inherit;
        }
      }

      &:hover {
        background-color: rgba(0,0,0,.1);
      }
    }

    &.left {
      float: left;
    }
  }

  // Navbar Search Form
  form {
    height: 100%;
  }

  .input-field {
    margin: 0;
    height: 100%;

    input {
      height: 100%;
      font-size: 1.2rem;
      border: none;
      padding-left: 2rem;

      &:focus, &[type=text]:valid, &[type=password]:valid,
      &[type=email]:valid, &[type=url]:valid, &[type=date]:valid {
        border: none;
        box-shadow: none;
      }
    }

    label {
      top: 0;
      left: 0;

      i {
        color: rgba(255,255,255,.7);
        transition: color .3s;
      }
      &.active i { color: $navbar-font-color; }
    }
  }
}



// Fixed Navbar
.navbar-fixed {
  position: relative;
  height: $navbar-height-mobile;
  z-index: 997;

  nav {
    position: fixed;
  }
}
@media #{$medium-and-up} {
  nav.nav-extended .nav-wrapper {
    min-height: $navbar-height;
  }
  nav, nav .nav-wrapper i, nav a.button-collapse, nav a.button-collapse i {
    height: $navbar-height;
    line-height: $navbar-line-height;
  }
  .navbar-fixed {
    height: $navbar-height;
  }
}
@media (max-width: 850px) {
  .c0re__header nav {
	height:140px;
  }
}





